import React, { Component } from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  FormGroup,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import 'react-widgets/dist/css/react-widgets.css';
import {Combobox} from 'react-widgets';
import axios from 'axios';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table'
import Grid from '@material-ui/core/Grid';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { saveAs } from 'file-saver';
import Image from '../../assets/img/brand/img2.jpg'
import Image2 from '../../assets/img/brand/img.jpg'
var id=0;

var dayAdvance=0,dieselFee=0,repairFee=0,deliveryFee=0,totalPays=0,financeFee=0;
class Forms extends Component {
  
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      timeout: 300,
      showMe: false, showDeliTbl:false,showPaymentRadio:false,showDeliRpBtn:false,showPayRpBtn:false,showAllPaymentTbl:false,
      showPaymentsTbl:false,showDieseltbl:false,
      lorries:[],deliveries:[],payments:[],dieselDetail : [],allPayments:[],
      selecedItem : '',paymentAmount : '',paymentType:'',
      liters:'',description:'',
      startDate: new Date(),
      endDate: new Date(),
      lorry:[],lorryf: [],
      deliTotal:0, payTotal:0,
      isPettySelect:false,
      lastPayId:[],
      paymentRpBtnText :'',
      

    };
    this.onChange=this.onChange.bind(this);
  }

  componentDidMount(){
    this.getLorries();
    this.getLastPayId();
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }

  getLorries(){
    fetch('/getlorries')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        lorries : json,
        
      });
    });
    
  }
  getLastPayId=async()=>{
    fetch('/getlastpayid')
    .then(res => res.json())
    .then(json =>{
      id=json[0].paymentsId;
      this.setState({ 
        lastPayId : json,
        
      });
      
    });
      

    
  }

    //fetch deliveries from database
    getDeliveries= async ()=>{
      var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
      var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
      await axios.post('/getdeliveriesbyrangebylorry',{
        startDate: stDate,
        endDate: enDate,
        vehicleNo: this.state.lorry.vehicleNumber
      }).then(res => {
        this.setState({
          deliveries:res.data
        })
      })
      var total=0;
      if(this.state.deliveries.length > 0){
        
        for(var i=0 ; i<this.state.deliveries.length; i++){
          total+= this.state.deliveries[i].deliveryPayment;
        }
        this.setState({
          deliTotal:total
        })
      }
      else{
        this.setState({
          deliTotal:0,
          payTotal:0
        })
      }
    }

//fetch monthly payment details
getpayments= async ()=>{
  this.setState({
    payTotal:0
  })
  var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
  var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
  await axios.post('/getmonthlypaymentsbylorry',{
    startDate: stDate,
    endDate: enDate,
    vehicleNumber:this.state.lorry.vehicleNumber,
    paymentType:this.state.paymentType
  })
  .then(res =>{
    this.setState({ 
      payments : res.data,
      
    });
  });
      var total=this.state.payTotal;
      if(this.state.payments.length > 0){
        
        for(var i=0 ; i<this.state.payments.length; i++){
          total+= this.state.payments[i].paymentsAmount;
        }
        
        this.setState({
          payTotal:total
        })

      }
      else{
        this.setState({
          payTotal:0
        })
      }
}
//get payment to update income form
getAllpayments= async ()=>{

  var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
  var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
  await axios.post('/getallpaymentsbylorry',{
    startDate: stDate,
    endDate: enDate,
    vehicleNumber:this.state.lorry.vehicleNumber,
  })
  .then(res =>{
    this.setState({ 
      allPayments : res.data,
      
    });
  });
  dayAdvance=0;
  dieselFee=0;
  repairFee=0;
  deliveryFee=0;
  totalPays=0;
      if(this.state.allPayments.length > 0){
        
        for(var i=0 ; i<this.state.allPayments.length; i++){
          if(!this.state.allPayments[i].paymentTypeType.toString().localeCompare('Day Advance')){
            console.log("Came")
            dayAdvance+=this.state.allPayments[i].paymentsAmount;
          }
          else if(!this.state.allPayments[i].paymentTypeType.toString().localeCompare('Maintain')){
            repairFee+=this.state.allPayments[i].paymentsAmount;
          }
          else if(!this.state.allPayments[i].paymentTypeType.toString().localeCompare('Delivery Fee')){
            deliveryFee+=this.state.allPayments[i].paymentsAmount;
          }
          else if(!this.state.allPayments[i].paymentTypeType.toString().localeCompare('Diesel')){
            dieselFee+=this.state.allPayments[i].paymentsAmount;
          }
          else if(!this.state.allPayments[i].paymentTypeType.toString().localeCompare('Finance')){
            financeFee+=this.state.allPayments[i].paymentsAmount;
          }
          totalPays+=this.state.allPayments[i].paymentsAmount;
        }
        
      }
      else{
        this.setState({
          payTotal:0
        })
      }
}
// Get Day Advance
getDayAdvance=(type)=>{

  this.setState({
    paymentType:type,
  });
}
getRepairFee=()=>{
  this.setState({
    paymentType:'2',
  });
}

  //Get Diesel Details from database
  getDieselDetails= async()=>{
  var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
  var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
      await axios.post('/getDieselDetails',{
        startDate : stDate,
        endDate : enDate,
        vehicleNumber : this.state.lorry.vehicleNumber
      }).then(res =>{
        this.setState({
          dieselDetail:res.data
        })
      })
  }
  //add dieselFee
  addDieselFee=async()=>{
    axios.post('/adddieselfee',{
      dieselFeeId : id+1,
      dieselFeeLiters:this.state.liters,
    }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('UnsuccessFull')){
        alert('Enter Liters');
      }
    })
  }

  //Adding a new paymet to database
   updatePayment= () => {
     
    var paymentTypeId;
    var selecedItem=this.state.selecedItem
    
    if(!selecedItem.localeCompare("Day Advance"))
    {
      paymentTypeId=1
    }
    else if(!selecedItem.localeCompare("Maintain"))
    {
      paymentTypeId=2
    }
    else if(!selecedItem.localeCompare("Delivery Fee"))
    {
      paymentTypeId=3
    }
    else if(!selecedItem.localeCompare("Finance"))
    {
      paymentTypeId=4
    }
    else{
      paymentTypeId=5;
    }
     axios.post('/addpayment',{
      paymentsAmount : this.state.paymentAmount,
      paymentTypeId: paymentTypeId,
      vehicleNo : this.state.lorryf.vehicleNumber,
      paymentsDescription:this.state.description
    }).then(res =>{
      this.getLastPayId();
      this.updatePettyCash();
      var status=res.data.status;
      if(!status.localeCompare('SuccessfullyAdded')){
        alert('Successfully Added');
      }
      else{
        alert('Bad Credentials')
      }
    })
    this.setState({
      payTotal:0
    })
    if(paymentTypeId==5){
      this.addDieselFee();
    }
    this.getpayments();
  }

  //Rendering diesel liter component on condition
  showElement(status,type)
  {
    this.setState({
      showMe:status,
      selecedItem:type
    })
  }
  //Rendering delivery table on condition
  showDeliveryTbl(status){
    this.setState({
      showPaymentsTbl:false,
      showDieseltbl:false,
      showDeliTbl:status,
      showAllPaymentTbl:false
    })
  }

  //Rendering payments table on condition
  showPaymentTables=(paymentType)=>{
      var btnText;
      if(paymentType=='1'){
        btnText="Day Advanced"
      }
      else if(paymentType=='2'){
        btnText="Maintain Fee"
      }
      else if(paymentType=='3'){
        btnText="Delivery Fee"
      }
      else if(paymentType=='4'){
        btnText="Finance Fee"
      }
      else if(paymentType=='6'){
        btnText="Payments"
      }
      else{
        btnText="Diesel Fee"
      }
   
    
      this.setState({
        showPaymentsTbl:true,
        showDieseltbl:false,
        showDeliTbl:false,
        showAllPaymentTbl:false,
        paymentType:paymentType,
        paymentRpBtnText: btnText
    },()=>{
      this.getpayments();
    })
  }

  //rendering diesel details on condition
  showDieselTable=() =>{
    this.setState({
      showDieseltbl:true,
      showDeliTbl : false,        
      showPaymentsTbl:false,
      showAllPaymentTbl:false
    },()=>{
      this.getDieselDetails();
    })
  }

  //showing payment types as radio buttons
  showPaymentRadio(status){
    this.setState({ 
      showPaymentRadio:status,
    })
  }
  //show Payment Table
  showAllPaymentTable(status){
    this.setState({
      showAllPaymentTbl:status,
      showPaymentsTbl:false,
      showDieseltbl:false,
      showDeliTbl:false,
    })
  }
  // updating pettycash book in database
  updatePettyCash= async ()=>{
    if(this.state.isPettySelect){
      if(window.confirm ("Update the petty cash book ?")==true){
        axios.post('/addpettycash',{
          paymentsId:id+1
        }).then(res =>{
          var status=res.data.status;
          if(!status.localeCompare('Successfull')){
          }
          else{
            alert('Failed To Update Petty Cash')
          }
        })
      }  
    }
  }

  //Clear button handler
  onClearClick=()=>{
      this.setState(
        {
          paymentAmount:'',
          liters:'',
          description:''
        }
      )
  }
  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  onRadioChange = (e) => {
    if (e==0)
  
    document.getElementById("literss").style.display="block";
     else
    document.getElementById("literss").style.display="none";

  return;
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }

  //Date change handlers
  handleStartDateChange = date =>{
    this.setState({
      startDate:date,
    },()=>{
      this.getDeliveries();
      this.getpayments();
      this.getAllpayments();
      this.getDieselDetails();
      
    })
    
  };
  handleEndDateChange = date =>{
    this.setState({
      endDate:date,
    },()=>{
      this.getDeliveries();
      this.getpayments();
      this.getDieselDetails();
      this.getAllpayments();
    })
    
  };

  //format date in table cell
  dateFormater(cell , row){
    var stDate = new Date(cell);
    var date = stDate.getFullYear() + '-' + (stDate.getMonth() + 1) + '-' + stDate.getDate();
     return `${date}`
   }
  // price formatter
  priceFormat(cell){
    return cell.toFixed(2);
  }

   //From here methods for calling reports from server begins
   onGetDeliveryReport=async()=>{
    var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
    var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
    await axios.post('/getdeliveryreportoflorry', {
      deliveries:this.state.deliveries,
      startDate :stDate,
      endDate : enDate,
      lorry:this.state.lorry,
      deliTotal:this.state.deliTotal
    })
      .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
      .then((res) => {
        const pdfBlob = new Blob([res.data], { type: 'application/pdf' });

        saveAs(pdfBlob, `DeliveryReport-${this.state.lorry.vehicleNumber}.pdf`);
      })
   }
   //get Income Report
   onGetIncomeReport=async()=>{
    var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
    var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
    var range="From : "+stDate+" To : "+enDate;
    await axios.post('/getincomereport', {
      dayAdvance:dayAdvance,
      maintain:repairFee,
      deliveryFee:deliveryFee,
      deliTotal:this.state.deliTotal,
      Diesel:dieselFee,
      delinums:this.state.deliveries.length,
      lorry:this.state.lorry,
      range:range
    })
      .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
      .then((res) => {
        const pdfBlob = new Blob([res.data], { type: 'application/pdf' });

        saveAs(pdfBlob, `IncomeReport-${this.state.lorry.vehicleNumber}.pdf`);
      })
   }
   onGetAllPayReport=async()=>{
    var text=this.state.paymentRpBtnText;
    var Route;
    var payop;
    var totalpays=0;
    if(!text.toString().localeCompare('Payments')){
      Route="getDailyPaymentReportOfLorry"
      payop=this.state.allPayments;
    }
    else{
      Route="getfilteredreportoflorry"
      payop=this.state.payments
    }
    //getting total for each pay type
    if(!text.toString().localeCompare('Payments')){
      totalpays=totalPays;
    }
    else if(!text.toString().localeCompare('Day Advanced')){
      totalpays=dayAdvance;
    }
    else if(!text.toString().localeCompare('Maintain Fee')){
      totalpays=repairFee;
    }
    else if(!text.toString().localeCompare('Finance Fee')){
      totalpays=financeFee;
    }
    else if(!text.toString().localeCompare('Diesel Fee')){
      totalpays=dieselFee;
    }
    else if(!text.toString().localeCompare('Delivery Fee')){
      totalpays=deliveryFee;
    }
    else{
      totalpays=0;
    }
   var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
   var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
   var range="From : "+stDate+" To : "+enDate;
   await axios.post(`/${Route}`, {
     payments : payop,
     payTotal :totalpays,
     lorry : this.state.lorry,
     range :range,
     payType :this.state.paymentRpBtnText
   })
     .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
     .then((res) => {
       const pdfBlob = new Blob([res.data], { type: 'application/pdf' });

       saveAs(pdfBlob, `PaymentReport-${this.state.lorry.vehicleNumber}.pdf`);
     })
    }
//Diesel Report
onGetDieselReport= async()=>{
  var stDate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
   var enDate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
   var range="From : "+stDate+" To : "+enDate;
   await axios.post(`/getdieselreport`, {
    diesel : this.state.dieselDetail,
    range: range,
    payTotal: dieselFee,
    lorry :this.state.lorry
     })
     .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
     .then((res) => {
       const pdfBlob = new Blob([res.data], { type: 'application/pdf' });

       saveAs(pdfBlob, `DieselReport-${this.state.lorry.vehicleNumber}.pdf`);
     })
}

  render() {
    return (
      <div className="animated fadeIn">

          <Row>
          <Col ys="3">
          <Card>
          <CardHeader >
            <FormGroup row>
                <Col md="4"></Col>
                <Col><h3>Delivery and Payment Details</h3></Col>
            </FormGroup>
              <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="company">Lorry</Label>
                    </Col>
                    <Col xs="5" md="3">
                    <div>
                      <Combobox
                        data={this.state.lorries}
                        suggest={true}
                        //valueField='companyName'
                        textField='vehicleNumber'
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value)=>{
                          this.setState({
                            lorry:value,
                            deliveries:[],
                            deliTotal:0,
                            payTotal:0
                          },()=>{
                            this.getDeliveries();
                            this.getpayments();
                            this.getAllpayments();
                            this.getDieselDetails();
                            
                          });
                        }
                        }
                        
                      />
                  </div>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                        <Col md="3">
                          <Label>What Do You Want ?</Label>
                        </Col>
                        <Col ys="2">
                        <FormGroup check>
                        <Input class="radio-grp1" onClick={()=>{this.showDeliveryTbl(true);
                          this.showPaymentRadio(false)}} type="radio"  name="tabletype" value="deliveries" />
                        <Label check >Get Delivery Details</Label>
                        </FormGroup>
                       <FormGroup></FormGroup>
                        <FormGroup check>
                        <Input class="radio-grp1" onClick={()=>{this.showPaymentRadio(true);this.showDeliveryTbl(false);this.showAllPaymentTable(true);this.showPaymentTables(6);this.showAllPaymentTable(true)}} type="radio" name="tabletype" value="Payments" />
                        <Label  check >Get Payment Details</Label>
                        </FormGroup>
                        </Col>
                  </FormGroup>
                  {
                    this.state.showPaymentRadio?
                  <FormGroup row>
                    <Col md="3">
                      <Label>Payment Type</Label>
                    </Col>
                    <Col>
                        <FormGroup check inline>
                        <Input  type="radio" name="payments" onClick={()=>{this.showPaymentTables(1)}} />
                        <Label  check >Day Advanced</Label>
                        </FormGroup>
                        <FormGroup check inline>
                        <Input   type="radio" name="payments" value="Maintain" onClick={()=>{this.showPaymentTables(2)}}/>
                        <Label  check >Mailtain</Label>
                      </FormGroup>
                      <FormGroup check inline>
                        <Input   type="radio"  name="payments" value="Delivery Fee" onClick={()=>{this.showPaymentTables(3)}} />
                        <Label  check >Delivery Fee</Label>
                      </FormGroup>
                      <FormGroup check inline>
                        <Input type="radio"  name="payments" value="Finance" onClick={()=>{this.showPaymentTables(4)}}/>
                        <Label check >Finance</Label>
                      </FormGroup>
                      <FormGroup check inline>
                        <Input  type="radio"  name="payments" value="Diesel" onClick={()=>{this.showDieselTable()}}/>
                        <Label  check >Diesel</Label>
                      </FormGroup>
                    </Col>
                  </FormGroup>
                  :null
                  }
                  <FormGroup row>
                    <Col md="3">
                    <Label>Capacity : {this.state.lorry.vehicleCapacity} cubes </Label>
                    </Col>
                    

                  </FormGroup>
                  <FormGroup row>
                 
                 <MuiPickersUtilsProvider utils={DateFnsUtils}>
                   <Grid container justify="space-around">
                     <Col >
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="Start Date"
                       format="yyyy-MM-dd"
                       value={this.state.startDate}
                       onChange={this.handleStartDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     <Col xs="2" md="3">
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="End Date"
                       format="yyyy-MM-dd"
                       value={this.state.endDate}
                       onChange={this.handleEndDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     </Grid>
                   </MuiPickersUtilsProvider>
                   </FormGroup>
              </CardHeader>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
              {
              this.state.showDeliTbl?
              <div>
                <FormGroup row><Col md="5"></Col><Col><strong>Deliveries</strong></Col></FormGroup>
              <BootstrapTable  data={this.state.deliveries} height="300" scrollTop={"bottom"} pagination exportCSV
                              tableStyle={ { border: '#0000FF 2.5px solid' } }
                              containerStyle={ { border: '#FFBB73 2.5px solid' } }
                              headerStyle={ { border: 'red 1px solid' } }
                              bodyStyle={ { border: 'green 1px solid' }}>
                  <TableHeaderColumn dataField="deliveryNoteNO" isKey>Delivery Note</TableHeaderColumn>
                  <TableHeaderColumn dataField="deliveryDate" dataFormat={this.dateFormater}>Date</TableHeaderColumn>
                  <TableHeaderColumn dataField="uploadingPlaceAddress">Unloading Place</TableHeaderColumn>
                  <TableHeaderColumn dataField="deliveryDistance">Distance (Km)</TableHeaderColumn>
                  <TableHeaderColumn dataField="deliveryPayment"
                  tdStyle={{'text-align':'right'}} dataFormat={this.priceFormat.bind(this)}>Payment</TableHeaderColumn>
                </BootstrapTable>
              </div>
                :null
                }
                <div>
                {
                  this.state.showPaymentsTbl?
                  <div>
                    <FormGroup row><Col md="5"></Col><Col><strong>{this.state.paymentRpBtnText} Payments</strong></Col></FormGroup>
                  <BootstrapTable data={this.state.payments} height="300" scrollTop={"bottom"} pagination exportCSV
                                  tableStyle={ { border: '#0000FF 2.5px solid' } }
                                  containerStyle={ { border: '#FFBB73 2.5px solid' } }
                                  headerStyle={ { border: 'red 1px solid' } }
                                  bodyStyle={ { border: 'green 1px solid' }}>
                    <TableHeaderColumn dataField="paymentsId" isKey>No</TableHeaderColumn>
                    <TableHeaderColumn dataField="paymentsDate" dataFormat={this.dateFormater}>Date</TableHeaderColumn>
                    <TableHeaderColumn dataField="paymentsDescription">Description</TableHeaderColumn>
                    <TableHeaderColumn dataField="paymentsAmount"
                    tdStyle={{'text-align':'right'}} dataFormat={this.priceFormat.bind(this)}>Amount</TableHeaderColumn>
                  </BootstrapTable>
                  </div>
                  :null
                }
                </div>
                <div>
                  {
                    this.state.showDieseltbl?
                    <div>
                      <FormGroup row><Col md="5"></Col><Col><strong>Diesel Fee Payments</strong></Col></FormGroup>
                    <BootstrapTable data={this.state.dieselDetail} height="300" scrollTop={"bottom"} pagination exportCSV
                                    tableStyle={ { border: '#0000FF 2.5px solid' } }
                                    containerStyle={ { border: '#FFBB73 2.5px solid' } }
                                    headerStyle={ { border: 'red 1px solid' } }
                                    bodyStyle={ { border: 'green 1px solid' }}>
                    <TableHeaderColumn dataField="dieselFeeId" isKey>No</TableHeaderColumn>
                    <TableHeaderColumn dataField="paymentsDate" dataFormat={this.dateFormater}>Date</TableHeaderColumn>
                    <TableHeaderColumn dataField="dieselFeeLiters">Liters</TableHeaderColumn>
                    <TableHeaderColumn dataField="paymentsAmount"
                    tdStyle={{'text-align':'right'}} dataFormat={this.priceFormat.bind(this)}>Amount</TableHeaderColumn>
                  </BootstrapTable>
                  </div>
                  :null
                  }
                </div>
                <div>
                  {
                    this.state.showAllPaymentTbl?
                    <div>
                      <FormGroup row><Col md="5"></Col><Col><strong>Payments</strong></Col></FormGroup>
                    <BootstrapTable data={this.state.allPayments} height="300" scrollTop={"bottom"} pagination exportCSV
                                    tableStyle={ { border: '#0000FF 2.5px solid' } }
                                    containerStyle={ { border: '#FFBB73 2.5px solid' } }
                                    headerStyle={ { border: 'red 1px solid' } }
                                    bodyStyle={ { border: 'green 1px solid' }}>
                    <TableHeaderColumn dataField="paymentsId" isKey>No</TableHeaderColumn>
                    <TableHeaderColumn dataField="paymentsDate" dataFormat={this.dateFormater}>Date</TableHeaderColumn>
                    <TableHeaderColumn dataField="paymentTypeType">PaymentType</TableHeaderColumn>
                    <TableHeaderColumn dataField="paymentsDescription">Description</TableHeaderColumn>
                    <TableHeaderColumn dataField="paymentsAmount"
                    tdStyle={{'text-align':'right'}} dataFormat={this.priceFormat.bind(this)}>Amount</TableHeaderColumn>
                  </BootstrapTable>
                  </div>
               :null
                  }
            </div>
            </CardBody>
            <CardFooter >
            {
                    this.state.showDeliTbl?
                    <FormGroup row>
                    <Col md="10">
                    </Col>
                  <Col >
                    <Button color="warning" onClick={()=>{this.onGetDeliveryReport()}}>Delivery Report</Button>
                  </Col>
                  </FormGroup>
                  : null
                  }
                {
                  (this.state.showPaymentRadio==true && this.state.showDieseltbl==false)?
                  
                  <div>
                    <FormGroup row>
                      <Col md="10">
                      </Col>
                      <Col>
                      <Button color="warning" onClick={()=>{this.onGetAllPayReport();}}>{this.state.paymentRpBtnText} Report</Button>
                      </Col>
                    </FormGroup>
                  </div>
                  :null
                }
                {
                  this.state.showDieseltbl?
                  <div>
                  <FormGroup row>
                    <Col md="10">
                    </Col>
                    <Col>
                      <Button color="warning" onClick={()=>{this.onGetDieselReport();}}>Diesel Fee Report</Button>
                    </Col>
                  </FormGroup>
                </div>
                :null
                }
            </CardFooter>
            <CardBody style={{backgroundImage:`url(${Image})`}}>
                <div>
                  
                    <div className="animated fadeIn">
                       <Row >
                       <Col  xs="3" ys="6">
                        </Col>
                          <Col xs="12" sm="6">
                           <Card>
                            <CardHeader>
                              <FormGroup row>
                                <Col>
                              <strong>Transport Incomes Of</strong>
                              </Col>
                              <Col>
                              <strong>{this.state.lorry.vehicleNumber}</strong>
                              </Col>
                              </FormGroup>
                            </CardHeader>
                            <CardBody >

                              <FormGroup row>
                                <Col xs="12" md="6">
                                <Label>Unloading Rate : </Label>
                                </Col>
                                <Col style={{textAlign : "right"}}>
                                <Label>{this.state.lorry.vehicleDeliRate}</Label>
                                </Col>
                              
                              </FormGroup>
                              <FormGroup row>                                                    
                                <Col xs="12" md="6">
                                <Label >Total Deliveries :</Label>
                                </Col>
                                <Col style={{textAlign : "right"}} >
                                  <Label>{this.state.deliveries.length}</Label>
                                </Col>                              

                            </FormGroup>
                              <FormGroup row>
                                <Col md="8">
                                <strong>Incomes</strong>
                                </Col>
                              
                              </FormGroup>

                         
                          <FormGroup row>
                            <Col xs="12" md="6">
                              <Label >Total Delivey Price </Label>
                            </Col>
                            <Col>
                            <Label>: Rs</Label>
                            </Col>
                            <Col style={{textAlign : "right"}} >
                            <Label>{this.state.deliTotal.toFixed(2)}</Label>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                                <Col md="8">
                                <strong>Expenses</strong>
                                </Col>
                              
                              </FormGroup>
                            <FormGroup row>

                                                      
                                 <Col xs="12" md="6">
                                  <Label >Total Day Advance </Label>
                                </Col>
                                <Col>
                                <Label>: Rs</Label>
                                </Col>
                                <Col style={{textAlign : "right"}} >
                                  <Label>{dayAdvance.toFixed(2)}</Label>
                                </Col>                              
                              
                              </FormGroup>
                         
                          <FormGroup row>
                            <Col xs="12" md="6">
                              <Label >Total Repair Fee </Label>
                            </Col>
                            <Col>
                            <Label>: Rs</Label>
                            </Col>
                            <Col style={{textAlign : "right"}} >
                            <Label>{repairFee.toFixed(2)}</Label>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Col xs="12" md="6">
                              <Label >Total Cash Payments </Label>
                            </Col>
                            <Col>
                            <Label>: Rs</Label>
                            </Col>
                            <Col style={{textAlign : "right"}} >
                            <Label>{deliveryFee.toFixed(2)}</Label>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Col xs="12" md="6">
                              <Label >Total Diesel Fee </Label>
                            </Col>
                            <Col>
                            <Label>: Rs</Label>
                            </Col>
                            <Col style={{textAlign : "right"}}  >
                            <Label>{dieselFee.toFixed(2)}</Label>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                                <hr/>
                        </FormGroup>
                        <FormGroup row>
                            <Col xs="12" md="6">
                              <Label ><strong>Net Income </strong></Label>
                            </Col>
                            <Col>
                            <Label>: Rs</Label>
                            </Col>
                            <Col style={{textAlign : "right"}} >
                            <Label >{((this.state.deliTotal)-(dayAdvance+dieselFee+repairFee+deliveryFee)).toFixed(2)}</Label>
                            </Col>
                        </FormGroup>
                    </CardBody>
                      <CardFooter>
                        <Col>
                          <Button color="info" onClick={()=>{this.onGetIncomeReport();}}>Income Report</Button>
                        </Col>
                      </CardFooter>
                  </Card>
                 </Col>
                 </Row>
       
                </div>
                </div>
              </CardBody>
              <CardFooter>
            

                
              </CardFooter>
          </Card>
          <Row>
          <Col xs="16" sm="10">
            <Card>
              <CardHeader>
                <strong>Expenses</strong>
                <small> Form</small>
              </CardHeader>
              <CardBody style={{backgroundColor:`#d9d9d9`}}>
              <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="selectSm">Lorry</Label>
                    </Col>
                    <Col xs="6" md="4">
                    <div>
                      <Combobox
                        data={this.state.lorries}
                        textField='vehicleNumber'
                        suggest={true}
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value) =>{
                            this.setState({
                              lorryf:value
                            },() => {
                              //this.getDeliveries();
                              //this.getpayments();
                          });
                        }
                      }
                      />
                    </div>
                    </Col>
                  </FormGroup>

                <FormGroup row>
                    <Col md="3">
                      <Label>Payment Type</Label>
                    </Col>
                    <Col xs="12" md="8">
                      <FormGroup check inline>
                        <Input className="form-check-input" onClick={()=>this.showElement(false,'Day Advance')} type="radio" id="inline-radio1" name="inline-radios" value="Day Advanced" />
                        <Label className="form-check-label" check htmlFor="inline-radio1">Day Advanced</Label>
                      </FormGroup>
                      <FormGroup check inline>
                        <Input className="form-check-input" onClick={()=>this.showElement(false,'Maintain')} type="radio" id="inline-radio2" name="inline-radios" value="Mailtain" />
                        <Label className="form-check-label" check htmlFor="inline-radio2">Maintain</Label>
                      </FormGroup>
                      <FormGroup check inline>
                        <Input className="form-check-input" onClick={()=>this.showElement(false,'Delivery Fee')} type="radio" id="inline-radio3" name="inline-radios" value="Delivery Fee" />
                        <Label className="form-check-label" check htmlFor="inline-radio3">Delivery Fee</Label>
                      </FormGroup>
                      <FormGroup check inline>
                        <Input className="form-check-input" onClick={()=>this.showElement(false,'Finance')} type="radio" id="inline-radio3" name="inline-radios" value="Finance" />
                        <Label className="form-check-label" check htmlFor="inline-radio3">Finance</Label>
                      </FormGroup>
                      <FormGroup check inline>
                        <Input className="form-check-input" onClick={()=>this.showElement(true,'Diesel')} type="radio" id="inline-radio4" name="inline-radios" value="Diesel" />
                        <Label className="form-check-label" check htmlFor="inline-radio4">Diesel</Label>
                      </FormGroup>
                    </Col>
                  </FormGroup>
                 
                    {
                    this.state.showMe?
                    <FormGroup row>
                  <Col md="3">
                  <Label htmlFor="liters">Liters</Label>
                  </Col>
                  <Col xs="12" md="9">
                  <Input type="text" name="liters" placeholder="Enter liters"
                  onChange={this.onChange} />
                  </Col>
                  </FormGroup>
                    :null
                    }
                  <FormGroup row>
                    <Col md="3">
                      <Label>Description</Label>
                    </Col>
                    <Col xs="12" md="8">
                      <Input type="textarea" name="description"  rows="9" value={this.state.description} onChange={this.onChange}
                             placeholder="Content..." />
                    </Col>
                  </FormGroup>
                <FormGroup>
                        <Label htmlFor="appendedPrependedInput">Amount</Label>
                        <div className="controls">
                          <InputGroup className="input-prepend">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>Rs</InputGroupText>
                            </InputGroupAddon>
                            <Input name="paymentAmount" size="16" type="text" 
                            onChange={this.onChange}/>
                            <InputGroupAddon addonType="append">
                              <InputGroupText>.00</InputGroupText>
                            </InputGroupAddon>
                          </InputGroup>
                        </div>
                      </FormGroup>
                      <FormGroup row>
                      <Col xs="16" md="10">
                      <FormGroup check inline>
                        
                        <Input className="form-check-input" onClick={()=>{
                          if(this.state.isPettySelect){
                            this.setState({
                              isPettySelect:false
                            })
                          }
                          else{
                            this.setState({
                              isPettySelect:true
                            })                            
                          }

                        }} type="checkbox" id="inline-checkbox" name="inline-checkbox"  />
                        <Label className="form-check-label" check htmlFor="inline-checkbox">Update PettyCash Book?</Label>
                        
                      </FormGroup>
                      </Col>  
                      </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={this.updatePayment}>Save</Button>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={this.onClearClick}>clear</Button>
                    </FormGroup>
                  </Col>
                  </FormGroup>
              </CardBody>
            </Card>
          </Col>
          </Row>
          </Col>
        </Row>
        
      </div>
    );
  }


}
function show(x)
{
  if (x==0)
  
    document.getElementById("literss").style.display="block";
  else
    document.getElementById("literss").style.display="none";

  return;
}

export default Forms;
